<template>
  <div>
    <el-row :gutter="24">
      <el-col :span="6">
        <img v-bind:src="options.img" height="100% " width="100%" @click="editCropper()" title=""
             class="img-circle img-lg"/>
      </el-col>
      <el-col :span="18">
        <el-form ref="form" :model="group" :rules="groupRules" label-width="80px">
          <el-form-item label="" prop="displayName">
            <el-input size="small" v-model="group.displayName"/>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
    <div style="text-align: right">
      <el-button type="primary" size="mini" @click="submitUser"></el-button>
      <el-button  size="mini" @click="gb" > </el-button>
    </div>
    <el-dialog :title="title" :visible.sync="imgEditOpen" width="800px" append-to-body @opened="modalOpened">
      <el-row>
        <el-col :xs="24" :md="12" :style="{height: '350px'}">
          <!-- <vue-cropper
              ref="cropper"
              :img="options.img"
              :info="true"
              :autoCrop="options.autoCrop"
              :autoCropWidth="options.autoCropWidth"
              :autoCropHeight="options.autoCropHeight"
              :fixedBox="options.fixedBox"
              @realTime="realTime"
              v-if="imgVisible"
          /> -->
        </el-col>
        <el-col :xs="24" :md="12" :style="{height: '350px'}">
          <div class="avatar-upload-preview">
            <img :src="previews.url" :style="previews.img"/>
          </div>
        </el-col>
      </el-row>
      <br/>
      <el-row>
        <el-col :lg="2" :md="2">
          <el-upload action="#" :http-request="requestUpload" :show-file-list="false" :before-upload="beforeUpload">
            <el-button size="small">
              
              <i class="el-icon-upload el-icon--right"></i>
            </el-button>
          </el-upload>
        </el-col>
        <el-col :lg="{span: 1, offset: 2}" :md="2">
          <el-button icon="el-icon-plus" size="small" @click="changeScale(1)"></el-button>
        </el-col>
        <el-col :lg="{span: 1, offset: 1}" :md="2">
          <el-button icon="el-icon-minus" size="small" @click="changeScale(-1)"></el-button>
        </el-col>
        <el-col :lg="{span: 1, offset: 1}" :md="2">
          <el-button icon="el-icon-refresh-left" size="small" @click="rotateLeft()"></el-button>
        </el-col>
        <el-col :lg="{span: 1, offset: 1}" :md="2">
          <el-button icon="el-icon-refresh-right" size="small" @click="rotateRight()"></el-button>
        </el-col>
        <el-col :lg="{span: 2, offset: 6}" :md="2">
          <el-button type="primary" size="small" @click="uploadImg()"> </el-button>
        </el-col>
      </el-row>
    </el-dialog>

  </div>
</template>

<script>

// import {VueCropper} from "vue-cropper";
import {add} from "@/api/data";
import {editGroup} from "@/api/data"

export default {
  name: "EditGroup",
  components: {
    // VueCropper,
  },
  props: ['group'],
  data(){
    return {
      imgEditOpen: false,
      // cropper
      imgVisible: false,
      // 
      title: "",
      options: {
        img: this.group.avatar, //
        autoCrop: true, // 
        autoCropWidth: 200, // 
        autoCropHeight: 200, // 
        fixedBox: true //  
      },
      previews: {},
      groupRules: {
        displayName: [
          {required: true, trigger: "blur", message: ""},
          {min: 6, message: "6", trigger: "blur"},
          {max: 12, message: "12", trigger: "blur"},
        ],
      },
    }
  },
  methods: {
    userEdit() {
      this.options.img = this.user.avatar
      this.userOpen = true;
    }
    ,
    // 
    editCropper() {
      this.imgEditOpen = true;
    }
    ,
    // 
    modalOpened() {
      this.imgVisible = true;
    }
    ,
    // 
    requestUpload() {
    }
    ,
    // 
    rotateLeft() {
      this.$refs.cropper.rotateLeft();
    }
    ,
    // 
    rotateRight() {
      this.$refs.cropper.rotateRight();
    }
    ,
    // 
    changeScale(num) {
      num = num || 1;
      this.$refs.cropper.changeScale(num);
    }
    ,
    // 
    beforeUpload(file) {
      if (file.type.indexOf("image/") == -1) {
        this.msgError(",JPGPNG");
      } else {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
          this.options.img = reader.result;
        };
      }
    }
    ,
    // 
    uploadImg() {
      this.$refs.cropper.getCropBlob(data => {
        let formData = new FormData();
        formData.append("file", data);
        add(formData).then(value => {
          this.group.avatar = process.env.VUE_APP_BASE_API + '/file/getFile?id=' + value.msg;
          this.group.avatarId = value.msg
          this.imgEditOpen = false
        });
      });
    }
    ,
    // 
    realTime(data) {
      this.previews = data;
    },
    submitUser(){
      let data = {
        userName: this.group.id,
        nickName: this.group.displayName,
        avatar: this.group.avatarId
      }
      console.log(this.group)
      editGroup(data).then(value => {
        this.$emit('grouprMethod');
      })
    },
    gb(){
      this.$emit('grouprMethod');
    }
  }
}
</script>

<style scoped>

</style>